.layout {
  width: 100%;
  max-width: 100%;
  height: 100vh;
  min-height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
}

.container {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
}

.pagesContainer {
  overflow: hidden;
  width: 100%;
  height: 100%;
}
